@import url(https://unpkg.com/bootstrap@4.3.1/dist/css/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about{
    background-color: #EEE;
}

.about p{
    color:#444;
    line-height: 1.7;
}

.about .AboutHeader{
    background-color: #17A2B8;
    width: 100%;
    height: 180px;
}

.about .AboutHeader h3{
    color: #EEE;
    padding: 5%;
    width: 50%;
}

.about .ourMessage .card{
    text-align: center;
    float: left;
    margin: 5%;
    width: 35%;
    padding: 2%;
}
.about .ourMessage .card img{
    width: 100px;
    height: 100px;
    margin: 7% auto;
    border-radius: 50%;
}
.about .ourMessage .card h4{
    color: #563D7C;
}
.about .ourMessage .blogCard{
    margin-top: 12%;
    background-color: #EEE;
    border: none;
}

.about .ourMessage .blogCard h2{
    text-align: left;
    color: #563D7C;
    margin: 5%;
    width: 100%;
}
.about .ourMessage .blogCard .blogButton{
    background-color: #563D7C;
    color: #EEE;
    border: 1px solid #EEE;
    padding: 1.5%;
    margin-top: 2%;
    width: 35%;
    border-radius: 5px;
    outline: none;
}
.about .ourMessage .blogCard .blogButton:hover{
    background-color: #EEE;
    color: #563D7C;
    border: 1px solid #563D7C;
}

@media (max-width: 767px){  /* phone screen */

    .about .AboutHeader h3{
        width: 70%;
    }
    
    .about .ourMessage .card{
        width: 90%;
    }
    .about .ourMessage .blogCard {
        margin-top: 1%;
    }
    .about .ourMessage .blogCard h2{
        margin-top: -2%;
        width: 80%;
    }
}

@media (min-width: 768px) and (max-width: 991px){ /* small screen */
    .about .AboutHeader h3{
        width: 70%;
    }
    
    .about .ourMessage .card{
        width: 90%;
    }
    .about .ourMessage .blogCard {
        margin-top: 1%;
    }
    .about .ourMessage .blogCard h2{
        margin-top: -2%;
        width: 80%;
    }
}
.NavLinks {
    color: rgba(255,255,255,.5);
    padding : .5rem;
    padding-right: .5rem;
    padding-left: .5rem;
    text-decoration: none;
    background-color: transparent;
    cursor: pointer;
} 
.NavLinks:hover {
    text-decoration: none;
    color: aliceblue;
}
.App{
    background-color: #2f3236;
}
.welcomeCard{
    border: 2px solid white;
    border-radius : 50px;
    width:90%;
    min-height: 280px;
    margin:40px auto ;
    text-align: left;
}
.welcomeTexts{
    float: left;
}
.welcomeCard h2{
    color: #EEE;
    margin: 8% 4% 0;
}
.welcomeCard h4{
    color: #FFF;
    margin: 1% 5%;
}
.welcomeCard img{
    margin-top: -2%;
    width: 41%;
}

.sideTitles{
    color: #17A2B8;
    margin-left: 12%;
    font-size: 20px;
    text-decoration: underline;
}
.swipperDiv{
    margin: 20px 0 20px 7%;
    width: 85%;
    display: inline-flex;
}
.swipperDiv > .Card{
    width: 85%;
    display: inline-flex;
}

@media (max-width: 767px){  /* phone screen */
    .welcomeCard img{
        margin: 10px -7% 0 0;
        width: 50%;
        margin-top: -25%;
        float: right;
    }
    .swipperDiv > .Card:nth-of-type(2n + 1){
        display: none !important;
    }

}

@media (min-width: 768px) and (max-width: 991px){ /* small screen */
    .welcomeCard img{
        margin: 10px -7% 0 0;
        width: 50%;
        margin-top: -25%;
        float: right;
    }
    .swipperDiv .Card:nth-child(3n + 1){
        display: none !important;
    }
}
.BookCard{
    height: 260px; 
    position: relative;
    margin: 7px;
    border: 1px solid #EEE;
    background-color: #EEE;
}
.BookCard img{
    width: 100%;
    height: 100%;
    padding: .5%;
}
.box{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.afterHover{
    width: 100%;
    height: 100%;
    text-align: center;
    color: #EEE;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.863);
}
.hoverContent{
    position: absolute;
    bottom: 0;
    padding: 20%;
}
.hoverContent > button{
    outline: none;
    border: 2px solid #EEE;
    background: none;
    color: #EEE;
    font-weight: 700;
    padding: 4%;
    border-radius: 5px;
    margin-top: 3%;
    width: 100%;
}

.hoverContent > button:hover{
    background-color: rgb(30, 75, 65);
    border: 2px solid rgb(30, 75, 65);
}
.imageHoverd{
    opacity: .8;
}

.footer{
    background-color: #021d3a;
}

.footer .firstPartation{
    float: left;
    width: 50%;
}
.footer .firstPartation > h1{
    color: #EEE;
    display: inline-block;
    margin: 4% 0;
    width: 100%;
    text-transform: uppercase;
}
.footer .firstPartation > h1 > span{
    color: rgb(5, 143, 143);
}
.footer .firstPartation > h1 .BookIcon{
    margin-right: 4%;
}

.footer .firstPartation > p{
    width: 85%;
    color: #969696;
    margin-left: 4%;
    line-height: 1.7;
}

.footer .firstPartation .fa{
    font-size: 40px;
    margin: 2.5%;
    color: rgb(5, 143, 143);
}

.footer .firstPartation .fa:hover{
    text-decoration: none;
    color: rgb(11, 133, 116, .4);
}

.footer .MostPopular,
.footer .AddressPartation{
    width: 25%;
    float: left;
}

.footer .MostPopular > span,
.footer .AddressPartation > span{
    display: block;
    color: #969696;
    margin-left: 10%;
}

.footer h3{
    color: #EEE;
    margin: 15% 8% 11%;
    font-size: 20px;
}

.footer .footerParagraph{
    color: #FFF;
    text-align: center;
    margin: 2%;
}

@media (max-width: 991px){ /* small screen */
    
    .footer .firstPartation{
        float: none;
        width: 100%;
    }
    .footer .firstPartation > h1{
        margin: 4% 0;
        width: 100%;
        font-size: 30px;
    }

    .footer .firstPartation > p{
        width: 95%;
        line-height: 1.5;
    }

    .footer .firstPartation .fa{
        font-size: 30px;
    }

    .footer .MostPopular,
    .footer .AddressPartation{
        width: 100%;
    }

    .footer h3{
        margin: 7% 0 2%;
    }
    
    .footer .footerParagraph{
        margin: 4%;
    }
}
.BookCard{
    background: none;
    border: none;
    display: inline-flex;
}
.imgHoveresDiv,
.imgDiv{
    width: 185px;
    height: 270px;
    margin: 1%;
    transform: scale(.8);
    transition: all .3s ease-in-out;
    text-align: center;
}

.imgHoveresDiv,
.imgDiv:hover{
    /* -webkit-transform: scale(1);
            transform: scale(1); */
}
.imgDiv img{
    width: 100%;
    height: 100%;
}

.hover{
    position: absolute;
    z-index: 9;
    top: 0;
    left: 0;
    text-align: center;
    background: #EEE;
    width: 390px;
    height: 300px;
    margin: 3% 1% 1%;
    padding: 0;
}

.hover .hoverImg{
    float: left;
    width: 50%;
}

.hover h4,
.hover span{
    margin-top: 4%;
    color: #444;
}

.hover button{
    margin: 5%;
}
.wraper{
    background: rgba(0, 0, 0, .75);
    height: 380px;
}

.contactDiv {
    height: 0px;
    color: white;
    text-align: center;
}
.contactDiv h2{
    padding: 20% 0 0;
}
@media (max-width: 767px){ /* small screen */

    .wraper{
        height: 300px;
    }
}
.MailContent{
    background-color: white;
    border-top: 5px solid #17A2B8;
    padding: 2%;
}

.MailContent form{
    float: left;
    width: 65%;
}

.MailContent input{
    width: 30%;
    margin: 1% .5%;
    padding: 1%;
    border-radius: 5px;
    outline: none;
    border: none;
    background-color: #F4F5F7;
}

.MailContent textarea{
    width: 92%;
    padding: 1%;
    border-radius: 5px;
    margin: 1% .5%;
    outline: none;
    border: none;
    background-color: #F4F5F7;
    height: 170px;
}
.MailContent .button{
    display: block;
    width: 40%;
    margin: 2% auto;
    background-color: #0069D9;
    color: white;
    border: 1px solid #0069D9;
    border-radius: 5px;
    font-weight: 600;
}
.MailContent .button:hover{
    background-color: #F4F5F7;
    color: #0069D9;
    border: 1px solid #0069D9;
}
.MailContent img{
    float: right;
    margin: 2% 0;
    width: 25%;
}
@media (max-width: 767px){ /* small screen */
    
    .MailContent form{
        margin-top: 20px;
        float: none;
        width: 100%;
    }
    
    .MailContent input{
        width: 32.3%;
    }

    .MailContent textarea{
        width: 99%;
        padding: 1%;
        height: 200px;
    }

    .MailContent img{
        display: none;
    }

}
.Clear{
    clear: both;
}
.AuthorCard{
    margin: 2% auto;
    background-color: #2F3236;
    width: 30%;
    padding: 2%;
    border: 1px solid #EEE;
    box-shadow: 3px 3px #EEE;
    border-radius: 15px;
    text-align: center;
    color: #EEE;
    padding-bottom: 2%;
}

.AuthorCard .imageDiv{
    width: 130px;
    height: 130px;
    border-radius: 50%;
    margin: 10% auto;
    padding: 3%;
    border: 2px solid #fcd303;
}

.AuthorCard img{
    width: 110px;
    height: 110px;
    border-radius: 50%;
    margin: auto;
}
.AuthorCard h4{
    padding: 1.7%;
    margin-top: 10%;
}

.AuthorCard p{
    color: #CCC;
    line-height: 1.8;
}

.AuthorCard span{
    padding: 2%;
    color: #ffe043;
}

.AuthorCard .link{
    color: #EEE;
    text-decoration: none;
}

.AuthorCard .getMore{
    display: block;
    margin: 3% auto 0;
    color: #EEE;
    outline: none;
    border: 1px solid #ffe043;
    background: none;
    padding: 2% 3%;
    font-size: large;
    border-radius: 10px;
}
.AuthorCard .getMore:hover{
    background-color: #ffe043;
    color: #000;
}

@media (max-width: 767px){  /* phone screen */

    .AuthorCard{
        width: 100%;
    }

}
@media (min-width: 768px) and (max-width: 991px){ /* small screen */

    .AuthorCard{
        width: 46%;
    }
}
.AuthorPage{
    display: flex;
    flex-wrap: wrap;
}
